import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";

export default function Home() {
  const [isOpen, setIsOpen] = useState(false);
  const [selectedProject, setSelectedProject] = useState(null);

  const projects = [
    { 
      title: "Stock Market Prediction (LSTM)", 
      tech: ["Python", "LSTM", "Deep Learning"],
      desc: "Designed and trained LSTM models for time series forecasting of stock prices using historical market data." 
    },
    { 
      title: "Fake Product Review Detection", 
      tech: ["NLP", "Python", "ML"],
      desc: "Developed a machine learning model to classify product reviews as real or fake using NLP techniques." 
    }
  ];

  return (
    <div className="min-h-screen bg-gray-950 text-white font-sans selection:bg-indigo-500">
      
      {/* NAVBAR */}
      <nav className="fixed top-0 w-full bg-black/80 backdrop-blur-md z-50 border-b border-gray-800">
        <div className="max-w-7xl mx-auto px-6 py-4 flex justify-between items-center">
          <h1 className="font-bold text-xl tracking-tighter">RAKESH G</h1>
          
          {/* Desktop Nav */}
          <div className="hidden md:flex gap-8 text-sm font-medium">
            <a href="#about" className="hover:text-indigo-400 transition">About</a>
            <a href="#experience" className="hover:text-indigo-400 transition">Experience</a>
            <a href="#projects" className="hover:text-indigo-400 transition">Projects</a>
          </div>

          {/* Mobile Hamburger Button */}
          <button onClick={() => setIsOpen(!isOpen)} className="md:hidden text-2xl">
            {isOpen ? "✕" : "☰"}
          </button>
        </div>

        {/* Mobile Menu */}
        <AnimatePresence>
          {isOpen && (
            <motion.div 
              initial={{ opacity: 0, height: 0 }}
              animate={{ opacity: 1, height: "auto" }}
              exit={{ opacity: 0, height: 0 }}
              className="md:hidden bg-gray-900 border-b border-gray-800 overflow-hidden"
            >
              <div className="flex flex-col p-6 gap-4 text-center">
                <a href="#about" onClick={() => setIsOpen(false)}>About</a>
                <a href="#experience" onClick={() => setIsOpen(false)}>Experience</a>
                <a href="#projects" onClick={() => setIsOpen(false)}>Projects</a>
              </div>
            </motion.div>
          )}
        </AnimatePresence>
      </nav>

      {/* HERO SECTION */}
      <section className="pt-32 pb-20 px-6 text-center">
        <motion.div initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }}>
          <h2 className="text-5xl md:text-7xl font-extrabold bg-gradient-to-r from-indigo-400 to-purple-500 bg-clip-text text-transparent">
            AI • ML • Data Science
          </h2>
          <p className="mt-6 text-xl text-gray-400 max-w-2xl mx-auto">
            Turning complex datasets into intelligent systems and actionable insights.
          </p>
        </motion.div>
      </section>

      {/* PROJECTS SECTION WITH HOVER ANIMATIONS */}
      <section id="projects" className="max-w-7xl mx-auto px-6 py-20">
        <h3 className="text-3xl font-bold mb-10">Featured Projects</h3>
        <div className="grid md:grid-cols-2 gap-8">
          {projects.map((proj, i) => (
            <motion.div 
              key={i}
              whileHover={{ scale: 1.02, translateY: -5 }}
              onClick={() => setSelectedProject(proj)}
              className="p-8 bg-gray-900/50 border border-gray-800 rounded-2xl cursor-pointer hover:border-indigo-500 transition-colors"
            >
              <h4 className="text-2xl font-bold text-indigo-400">{proj.title}</h4>
              <div className="flex gap-2 mt-3">
                {proj.tech.map(t => <span key={t} className="text-xs bg-gray-800 px-2 py-1 rounded text-gray-400">{t}</span>)}
              </div>
              <p className="mt-4 text-gray-400">Click to read more...</p>
            </motion.div>
          ))}
        </div>
      </section>

      {/* PROJECT MODAL POPUP */}
      <AnimatePresence>
        {selectedProject && (
          <div className="fixed inset-0 z-[100] flex items-center justify-center p-6">
            <motion.div 
              initial={{ opacity: 0 }} animate={{ opacity: 1 }} exit={{ opacity: 0 }}
              onClick={() => setSelectedProject(null)}
              className="absolute inset-0 bg-black/90 backdrop-blur-sm"
            />
            <motion.div 
              initial={{ scale: 0.9, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.9, opacity: 0 }}
              className="relative bg-gray-900 border border-gray-700 p-8 rounded-3xl max-w-lg w-full shadow-2xl"
            >
              <h3 className="text-3xl font-bold text-indigo-400">{selectedProject.title}</h3>
              <p className="mt-6 text-gray-300 leading-relaxed">{selectedProject.desc}</p>
              <button 
                onClick={() => setSelectedProject(null)}
                className="mt-8 w-full py-3 bg-indigo-600 rounded-xl font-bold hover:bg-indigo-700 transition"
              >
                Close Details
              </button>
            </motion.div>
          </div>
        )}
      </AnimatePresence>

      <footer className="py-10 text-center text-gray-600 text-sm border-t border-gray-900">
        © 2026 Rakesh G | Data Science Student
      </footer>
    </div>
  );
}